<template>
  <div class='blocks'>
      <h2>Visuals Of Oahu, View More..</h2>
      <div id="holdContainers">
          <div id="map"> 
              <my-map> </my-map>
          </div>
        <div id="camera"> </div>
      </div>
      <my-button :runFunction="function(){}" :description="something"> </my-button>
  </div>
</template>

<script>
export default {
  name: 'camera-block',
  props: {
      direct: {
          type: String,
          required: true
      }
  },
  data () {
    return {
        something: 'Go to Cameras & Map'
    }
  }
}
</script>

<style scoped>
.blocks {
    background: #3AAFA9;
} /* OOGA BOOGA CHA */

#holdContainers {
    display: inline-flex;
    width: 100%;
    justify-content: center;
    height: fit-content;
}

#map,#camera {
    width: 30rem;
    height: 18rem;
    border: 3px solid black;
    border-radius: 2rem;
    overflow: hidden;
    margin: auto 1rem;
}

h2 {
    display: inline-block;
    text-align: center;
    font-size: 2rem;
    padding: 1.5rem;
    font-weight: bold;
}

#camera {
    background-image: url('http://cctv.cdn.goakamai.org//SnapShot/320x240/TL-0037.jpg?icx=1547663897423');
    background-size: cover;
    background-position: center;
}

</style>


