<template>
  <div id="cameraTab">
      <h2>{{slogan}}</h2>
      <div id="reportContainer">
          <div class="pin" v-for="pins in GetItem">
              <img src="./../../../../assets/Map-Pin.png" alt="A google pin" class="pinImg">

          </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'report-pins',
  data () {
    return {
        GetItem: []
    }
  },
  created() {
      this.$http.get('https://dnvvqevqxf.execute-api.us-west-2.amazonaws.com/dev/get').then(function(data){
          let tempData = JSON.parse(data.data.body); //parses it to Objects as its just string in an object
          this.GetItem = tempData.message.rows;
      })
      .catch(err => {
          console.log(err);
      }); 
  },
  methods: {

  }
}
</script>

<style scoped>

p {
    text-align: center;
}

h2 {
    text-align: center;
    font-size: 2rem;
}

#reportContainer {
    display: flex;
    flex-wrap: wrap;
}

#cameraTab {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    background: linear-gradient(45deg, rgba(34,193,195,1) 0%, rgba(207,82,66,1) 20%, rgba(217,62,153,1) 50%, rgba(150,89,210,1) 80%, rgba(253,187,45,1) 100%);
    background-size: 400%;
    animation-name: Gradient;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}

input {
    width: 50%;
    height: 1.5em;
    padding: 1rem;
    margin: 0 auto 1rem;
    border-radius: 3em;
    border: 1px solid black;
    font-size: 1.5rem;
    background: transparent;
}

.pin {
    width: 15rem;
    height: 10rem;
    position: relative;
    border: 1px solid black;
    margin: 1rem auto;
    background: white;
    border-radius: 1rem;
}

.pinImg {
    position:absolute;
    height: 30%;
}

.pinImg:nth-child(odd) {
    top: -8%;
    left: -3%;
    transform: rotate(-45deg);
}

.pinImg:nth-child(even) {
    bottom: -8%;
    right: -3%;
    transform: rotate(45deg);
}

#cameraContainer {
    height: -webkit-fill-available;
}

@media screen and (min-width: 600px) and (max-device-width: 900px) and (orientation:portrait) {
    h2 {
        font-size: 4rem;
    }
    input {
        width: 65%;
        height: 4rem;

    }
}
</style>



